


















import Vue from "vue";
import ListCityItemSection from "@/components/Advanced-Components/List-City-Item-Section.vue";
import draggable from "vuedraggable";
import store from "@/store";

export default Vue.extend({
  name: "List-City-Section",
  data() {
    return {
      drag: false,
      value: store.state.WeatherData,
      dragOptions: {
        animation: 150,
        group: "description",
        disabled: false,
        ghostClass: "ghost"
      }
    };
  },
  components: {
    ListCityItemSection,
    draggable
  },
  computed: {
    WeatherData() {
      return store.state.WeatherData;
    }
  },
  watch: {
    value() {
      store.commit("updateWeatherData", this.value);
    },
    WeatherData() {
      this.value = this.WeatherData;
    }
  }
});
